<template>
  <div class="modal-form-wrapper">
    <form>
      <div class="form-group">
        <label for="num">貨號</label>
        <input type="text" class="form-control" id="num" placeholder="貨號" v-model="num">
        <small id="numTip" class="form-text text-muted">{{ tip_num }}</small>
      </div>
      <div class="form-group">
        <label for="named">描述</label>
        <input type="text" class="form-control" id="named" placeholder="描述" v-model="named">
        <small id="numTip" class="form-text text-muted">{{ tip_named }}</small>
      </div>
      <div class="form-group row">
        <div class="col-sm-4">
          <label for="price">零售價</label>
          <input type="text" class="form-control" id="price" placeholder="價格" v-model="price">
        <small id="numTip" class="form-text text-muted">{{ tip_price }}</small>
        </div>
        <div class="col-sm-4">
          <label for="unit">單位</label>
          <select class="form-control" id="unit" v-model="unit">
            <option v-for="u in units" :key="u.flag" :value="u.flag">{{ u.named }}</option>
          </select>
        </div>
        <div class="col-sm-4">
        </div>
      </div>
      <div class="form-group">
        <label for="named">標簽</label>
        <div class="tag-wrapper" v-if="tags.length > 0">
          <div class="tag tag-def d-inline hand" 
            v-for="t in tags" :key="t.named" 
            :class="{ 'tag-active': inList(t.id, select) }" 
            @click="tagChoise(t.id)"
          >
            {{ t.named }}
          </div>
        </div>
        <small id="numTip" class="form-text text-muted">{{ tip_tag }}</small>
      </div>
    </form>
  </div>
</template>

<script>
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'FreightPlusForm',
  props: {
    isPlus : {
      type: Boolean
    }
  },
  data() {
    return {
      select: [ ],
      tags: [],

      orderNum: '',

      //
      num: '',
      tip_num: '',

      named: '',
      tip_named: '',

      price: '',
      tip_price: '',
      
      unit: 0,

      tip_tag: '',

      activeId: 0
    }
  },
  computed: {
    units() {
      return view.unitList.map((e) => {
        return {
          flag: e[0],
          named: e[1]
        }
      })
    }
  },
  mounted() {
    this.loadTag()
  },
  methods: {
    locationData(data) {
      if ((data != null) && (data != undefined)) {
        this.named = data.named
        this.price = data.price
        this.unit = data.unit
        this.num = data.num
        this.select = []

        this.orderNum = data.num

        if ((data.tag != null) && (data.tag != undefined)) {
          const res = []
          for (const i of data.tag) {
            res.push(i.id)
          }
          for (const t of this.tags) {
            if (tool.inList(t.id, res)) {
              this.select.push(t.id)
            }
          }
        }
        this.activeId = data.id
      }
    },
    loadTag() {
      conn.tag.getList_Tag({}, true).then(res => {
        if (conn.judge_response(res)) {
          this.tags = res
          this.select.push(res[0].id)
        }
      })
    },
    inList(n, L) {
      return tool.inList(n, L)
    },
    tagChoise(id) {
      const index = tool.indexList(id, this.select)
      if (index >= 0) {
        
        if (this.select.length <= 1) {

          tool.showToast('提示：請至少選擇壹個標簽。')
        } else {
          this.select.splice(index, 1)
        }
      } else {
        this.select.push(id)
      }
    },
    tipClose(area) {
      setTimeout(() => {
        if (area == 'num') {
          this.tip_num = ''
        }
        else if (area == 'named') {
          this.tip_named = ''
        }
        else if (area == 'price') {
          this.tip_price = ''
        }
      }, 4200)
    },
    // 
    tipForm(res, area) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (area == 'num') { this.tip_num = show }
      else if (area == 'named') { this.tip_named = show }
      else if (area == 'price') { this.tip_price = show }

      this.tipClose(area)

    },
    validateForm() {
      let allow = true 
      
      let res = validate.val_empty(this.num, '貨號')
      if (res == true) {
        res = validate.val_char(this.num, '貨號')
        this.tipForm(res, 'num')
      } else { this.tipForm(res, 'num') }
      if (res != true) { allow = false }
      
      res = validate.val_empty(this.named, '描述')
      if (res == true) {
        res = validate.val_char(this.named, '描述')
        this.tipForm(res, 'named')
      } else { this.tipForm(res, 'named') }
      if (res != true) { allow = false }

      res = validate.val_empty(this.price, '零售價')
      if (res == true) {
        res = validate.val_char_price(this.price, '零售價')
        this.tipForm(res, 'price')
      } else { this.tipForm(res, 'price') }
      if (res != true) { allow = false }

      return allow
    },
    _validateSame(data, id = 0) {
      const condition = {
        num: data.num,
      }
      return new Promise((resolve, reject) => {
        conn.freight.getList_Freight(condition, true).then(res => {
          
          if (res.length > 0) {
            if (id == 0) {
              resolve(false)
            } else {

              const e = []
              for (const i of res) {
                
                if (i.id != id) {
                  e.push(i)
                }
              }
              if (e.length > 0) { resolve(false) }
            }
          }
          resolve(true)
        }).catch(err => {
          reject(false)
          tool.showToast('網絡連接失敗。')
        })
      })
    },
    _submit(data) {
      const _this = this
      if (this.isPlus) {
        this._validateSame(data).then(res => {
          if (res) {
            this.$emit('plus_Father', data)
          } else {
            tool.showToast('已存在相同貨號的產品了。')
          }
        })
      } else {
        this._validateSame(data, this.activeId).then(res => {
          if (res) {
            this.$emit('update_Father', data, this.activeId)
          } else {
            tool.showToast('這個貨號的產品已經存在了。')
          }
        })
      }
    },
    submitForm() {
      const submit = this.validateForm()
      if (submit == true) {

        const data = {
          num: this.num,
          named: this.named,
          price: this.price,
          unit: this.unit,
          tags: this.select.join(',')
        }

        this._submit(data)
      }
    }
  },
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"


</style>


