<template>
  <button 
    class="btn btn-light btn-sm pb-0 float-right mr-2 tool-tip" 

    data-toggle="tooltip" data-placement="top" title="重新整理"
    @click="clearCondition">
    <i class="fa fa-refresh"></i>
  </button>
</template>

<script>
import tool from '@/common/es/tool'
export default {
  name: 'ClearFilterOption',
  methods: {
    clearCondition() {
      this.$emit('clearCondition_Father')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


