<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="freightPlusModal">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <!-- div class="modal-header">
          <h5 class="modal-title">Modal title</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div -->
        <div class="modal-body">
          <freight-plus-form ref="plusForm" :isPlus="true" :_freight="freight" @plus_Father="_plus"></freight-plus-form>
        </div>
        <div class="modal-footer">
          
          <button class="btn btn-main" @click="submitFreightPlus">保存</button>
            
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import FreightPlusForm from './FreightPlusForm.vue'
export default {
  components: { FreightPlusForm },
  name: 'FreightPlus',
  props: {
    freight: {
      type: Object
    }
  },
  methods: {
    submitFreightPlus() {

      this.$refs.plusForm.submitForm()
    },
    _signelRefreash() {
      this.$emit('signelRefreash_Father')
    },
    _plus(data) {

      conn.freight.add(data).then(res => {
        if (conn.judge_response(res)) {
          
          tool.showToast('新增成功！！！', 2100)
          setTimeout(() => {
            tool.closeAllModal()
            this._signelRefreash()
          }, 700)
        }
      })
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


